}

li+li {
    margin-top: .25em
}

dl {
    padding: 0
}

dl dt {
    padding: 0;
    margin-top: 16px;
    font-size: 1em;
    font-style: italic;
    font-weight: 600
}

dl dd {
    padding: 0 16px;
    margin-bottom: 16px
}

table {
    display: block;
    width: 100%;
    overflow: auto;
    margin-top: 20px;
}

table th {
    font-weight: 600
}

table td,
table th {
    padding: 6px 13px;
    border: 1px solid #1d2f2f;
}

table tr {
    border-top: 1px solid #1d2f2f;
}

img {
    max-width: 100%;
    box-sizing: initial;
}

img[align=right] {
    padding-left: 20px
}

img[align=left] {
    padding-right: 20px
}

code {
    padding: .2em .4em;
    margin: 0;
    font-size: 85%;
    background-color: rgba(27, 31, 35, .05);
    border-radius: 3px
}

pre {
    word-wrap: normal
}

pre>code {
    padding: 0;
    margin: 0;
    font-size: 100%;
    word-break: normal;
    white-space: pre;
    background: 0 0;
    border: 0
}

.highlight {
    margin-bottom: 16px
}

.highlight pre {
    margin-bottom: 0;
    word-break: normal
}

.highlight pre,
pre {
    padding: 16px;
    overflow: auto;
    font-size: 85%;
    line-height: 1.45;
    background-color: #f6f8fa;
    border-radius: 3px
}

pre code {
    display: inline;
    max-width: auto;
    padding: 0;
    margin: 0;
    overflow: visible;
    line-height: inherit;
    word-wrap: normal;
    background-color: initial;
    border: 0
}

.commit-tease-sha {
    display: inline-block;
    font-family: SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
    font-size: 90%;
    color: #444d56
}

.full-commit .btn-outline:not(:disabled):hover {
    color: #005cc5;
    border-color: #005cc5
}

.blob-wrapper {
    overflow-x: auto;
    overflow-y: hidden
}

.blob-wrapper-embedded {
    max-height: 240px;
    overflow-y: auto
}

.blob-num {
    width: 1%;
    min-width: 50px;
    padding-right: 10px;
    padding-left: 10px;
    font-family: SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
    font-size: 12px;
    line-height: 20px;
    color: rgba(27, 31, 35, .3);
    text-align: right;
    white-space: nowrap;
    vertical-align: top;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none
}

.blob-num:hover {
    color: rgba(27, 31, 35, .6)
}

.blob-num:before {
    content: attr(data-line-number)
}

.blob-code {
    position: relative;
    padding-right: 10px;
    padding-left: 10px;
    line-height: 20px;
    vertical-align: top
}

.blob-code-inner {
    overflow: visible;
    font-family: SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
    font-size: 12px;
    color: #24292e;
    word-wrap: normal;
    white-space: pre
}

.pl-token.active,
.pl-token:hover {
    cursor: pointer;
    background: #ffea7f
}

